.Button {
    padding: 8px 16px;
    border-radius: 5px;
    background-color: #2cbb7f;
    transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
    text-align: center;
    display: inline-block;
    color: #fff;
    border: 3px solid #2cbb7f;
    text-decoration: none;
    cursor: pointer;
    font-family: inherit;
    font-size: 18px;
    line-height: 24px;
    font-style: normal;
    font-weight: 500;
    min-width: 180px;
    box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
      0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
}

.Button:hover,
.Button:focus {
    background-color: transparent;
    color: #212121;
    border: 3px solid #2cbb7f;
}

.ButtonContainer {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 10px;
}